"use client";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { useToast } from "@/hooks/use-toast";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import {
    Form,
    FormControl,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { motion, AnimatePresence } from "framer-motion";
import { Building2, Mail, Phone, MapPin, Link, FileText, ArrowRight, CircleDashed } from "lucide-react";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { cn } from "@/lib/utils";
import { useRouter } from "next/navigation";
import { useState, useEffect } from "react";

const organizerSchema = z.object({
    organizerType: z.enum(["company", "university"]),
    companyName: z.string().min(3, "Name must contain at least 3 characters"),
    contactPerson: z.string().min(3, "Name must contain at least 3 characters"),
    email: z.string().email("Invalid email address"),
    phone: z.string().regex(/^\d{10}$/, "Phone must be 10 digits"),
    address: z.string().min(5, "Address must contain at least 5 characters"),
    website: z.string().url("Invalid website URL"),
    document: z.instanceof(File, { message: "Authorization document is required" })
});

const OrganizerRegisterForm = () => {
    const { toast } = useToast();
    const router = useRouter();
    const [isLoading, setIsLoading] = useState(true);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [emailCheckLoading, setEmailCheckLoading] = useState(false);

    // استخراج رابط API من المتغيرات البيئية
    const API_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:3001";
    console.log(`api url  => ${API_URL}`)
    const form = useForm<z.infer<typeof organizerSchema>>({
        resolver: zodResolver(organizerSchema),
        defaultValues: {
            organizerType: "company",
            companyName: "",
            contactPerson: "",
            email: "",
            phone: "",
            address: "",
            website: "",
        },
    });

    useEffect(() => {
        const timer = setTimeout(() => {
            setIsLoading(false);
        }, 1000);

        return () => clearTimeout(timer);
    }, []);

    // دالة للتحقق من البريد الإلكتروني
    const checkEmailExists = async (email: string) => {
        try {
            setEmailCheckLoading(true);
            const response = await fetch(`${API_URL}/api/check-email?email=${email}`);

            if (!response.ok) {
                throw new Error("Failed to check email");
            }

            const data = await response.json();
            return data.exists;
        } catch (error) {
            toast({
                title: "Error",
                description: "Failed to verify email. Please try again.",
                variant: "destructive"
            });
            return false;
        } finally {
            setEmailCheckLoading(false);
        }
    };

    const onSubmit = async (values: z.infer<typeof organizerSchema>) => {
        setIsSubmitting(true);

        try {
            // التحقق من البريد الإلكتروني أولاً
            const emailExists = await checkEmailExists(values.email);

            if (emailExists) {
                toast({
                    title: "Email Already Registered",
                    description: "This email is already associated with an account. Please use a different email.",
                    variant: "destructive",
                    duration: 5000
                });
                return;
            }

            // 1. إنشاء FormData لإرسال البيانات (خاصة الملف)
            const formData = new FormData();
            formData.append('organizerType', values.organizerType);
            formData.append('companyName', values.companyName);
            formData.append('contactPerson', values.contactPerson);
            formData.append('email', values.email);
            formData.append('phone', values.phone);
            formData.append('address', values.address);
            formData.append('website', values.website);
            formData.append('document', values.document);

            // 2. إضافة حقل role لتحديد نوع المستخدم
            formData.append('role', 'host'); // تحديد أن هذا التسجيل للمضيفين

            const response = await fetch(`${API_URL}/api/register/organizer`, {
                method: "POST",
                body: formData,
                credentials: "include", // ضروري لإرسال الكوكيز
            });

            if (response.ok) {
                const data = await response.json();

                toast({
                    title: "Registration Successful",
                    description: "Your organization has been registered successfully!",
                    duration: 5000,
                    className: "bg-green-600 text-white border-0",
                });

                // 3. توجيه المستخدم بعد التسجيل الناجح
                setTimeout(() => {
                    router.push("/host/dashboard");
                }, 1000);
            } else {
                const errorData = await response.json();
                throw new Error(errorData.message || "Registration failed");
            }
        } catch (error: any) {
            toast({
                title: "Registration Failed",
                description: error.message || "An error occurred during registration",
                variant: "destructive",
                duration: 5000
            });
        } finally {
            setIsSubmitting(false);
        }
    };

    if (isLoading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-indigo-50 to-blue-100">
                <motion.div
                    animate={{ rotate: 360 }}
                    transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                    className="flex flex-col items-center"
                >
                    <CircleDashed className="h-16 w-16 text-blue-600" />
                    <p className="mt-4 text-lg font-medium text-gray-700">Loading registration form...</p>
                </motion.div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gradient-to-br from-indigo-50 to-blue-100 p-4 md:p-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, ease: "easeOut" }}
                className="max-w-4xl w-full mx-auto bg-white rounded-2xl shadow-xl overflow-hidden"
            >
                <div className="relative">
                    {/* Form container */}
                    <div className="relative z-10 p-8 md:p-12">
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ delay: 0.2 }}
                            className="text-center mb-12"
                        >
                            <motion.div
                                whileHover={{ scale: 1.05 }}
                                whileTap={{ scale: 0.95 }}
                                className="inline-block mb-6"
                            >
                                <Building2 className="mx-auto h-16 w-16 text-blue-600 p-3 bg-blue-50 rounded-full" />
                            </motion.div>
                            <motion.h1
                                initial={{ y: -10 }}
                                animate={{ y: 0 }}
                                transition={{ delay: 0.3 }}
                                className="text-3xl md:text-4xl font-bold text-gray-900 mb-2"
                            >
                                Host Registration
                            </motion.h1>
                            <motion.p
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                transition={{ delay: 0.4 }}
                                className="text-gray-600 text-lg"
                            >
                                For universities, companies, and certified institutions
                            </motion.p>
                        </motion.div>

                        <Form {...form}>
                            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
                                {/* ... باقي الكود بدون تغيير ... */}
                            </form>
                        </Form>
                    </div>
                </div>
            </motion.div >
        </div >
    );
};

export default OrganizerRegisterForm;